.grid-main-bottom {
  display: grid;
}
.grid-main-bottom__banner {
  max-width: 1250px;
  margin: 0 auto;
  width: 100%;
}
.grid-main-bottom__rubric {
  margin: 16px 0 0 0;
}
@media (max-width: 767px) {
  .grid-main-bottom__rubric {
    grid-row: 1;
    margin: 0 0 16px 0 !important;
  }
}
.grid-main-bottom__inner {
  max-width: 1250px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 15px;
  margin-top: 15px;
}
@media (max-width: 768px) {
  .grid-main-bottom__inner {
    margin-top: 20px;
  }
}
.grid-main-bottom__parthner-top img {
  width: 100%;
