.grid-main-bottom__right-parthner-1 img, .grid-main-bottom__right-parthner-2 img, .grid-main-bottom__parthner-bottom img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.grid-main-bottom__while-card {
  grid-column: 1/5;
  margin-bottom: 4px;
}

